.checkbox {
  position: absolute;
  height: 32px;
  width: 32px;
  top: 80px;
  right: 15px;
  z-index: 5;
  opacity: 0;
  cursor: pointer;
}
.checkbox:checked ~ .header-nav__menu-mobile {
  transform: translateX(0%);
}
.checkbox:checked + label .header-nav__btn-line:nth-child(1) {
  transform: translateY(0) rotate(45deg);
}
.checkbox:checked + label .header-nav__btn-line:nth-child(2) {
  transform: translateY(0) rotate(-45deg);
}
.checkbox:checked + label .header-nav__btn-line:nth-child(3) {
  opacity: 0;
}

.header-nav__btn {
  display: none;
  position: relative;
  width: 18px;
  height: 16px;
  border: none;
  cursor: pointer;
}
