.header-nav__btn-line {
  position: absolute;
  width: 18px;
  height: 4px;
  left: 0px;
  border-radius: 4px;
  background-color: white;
  transition: 0.3s ease all;
}

.header-nav__btn-line-short {
  width: 10px;
  left: 8px;
}

.header-nav__btn-line:nth-child(1) {
  transform: translateY(-10px);
}

.header-nav__btn-line:nth-child(3) {
  transform: translateY(10px);
}

